<script type="es6">
import HeadTop from "../../components/Header";
import { mapActions, mapState } from "vuex";
import Web3 from "web3";
export default {
  data() {
    return {
      lang: "en",
      currency: "",
      link: "--",
      coinBalance: 0,
      isApprove: false,
      isFiveApprove: false,
      stakeAmount: null,
      withdrawAmount: "",
      timer: null,
      timer2: null,
      show: false,
      isHome: true,
      nbn: 100,
      isApproveGrcc: false,
      showCionList: false,
      cionType: "FLW",
    };
  },
  computed: {
    ...mapState("main", [
      // "priceHMD",
      // "priceCNTM",
      "hmdInfo",
      "account",
      "balance",
      "mxtCoinContract",
      "mxtContract",
      "hmdBalance",
      "FiveCoinContract",
      "GCoinContract",
      "GBalance",
      "aesBalance"
    ]),
  },
  components: {
    HeadTop,
  },
  methods: {
    ...mapActions("main", [
      // "add_price",
      "add_hmdInfo",
      "add_mxtCoinContract",
      "add_hmdCoinContract",
      "add_gCoinContract",
      "add_aesCoinContract"
    ]),
    openCionList() {
      this.showCionList = !this.showCionList;
    },
    changeCion(type) {
      this.cionType = type;
      this.showCionList = false;
      if (type == "five") {
        var Price2 = this.hmdInfo.fivePrice;
      } else {
        var Price2 = this.hmdInfo.GrccPrice;
        this.GrccisApproveFun();
      }
      var prex = 4;
      var price = Number(Price2) * Number(this.stakeAmount);
      if (this.stakeAmount) {
        // info.fivePrice =null;
        if (parseInt(price) == price) {
          this.nbn = price;
          return;
        }
        let len = 1;
        let i = 0;
        while (parseInt(price) != price) {
          price = price * 10;
          len *= 10;
          i++;
        }
        let last = Math.abs(i <= prex ? 0 : price % Math.pow(10, i - prex));
        let d1 = price - last;
        if (last > 0) {
          d1 = d1 + Math.pow(10, i - prex);
        }
        d1 = d1 / len;
        this.nbn = d1;
      } else {
        this.nbn = null;
      }
    },
    showPop: function (arg, callback) {
      if (arg == "show") {
        this.show = true;
      }
    },
    pirceInput(type) {
      if (type == 1) {
        let str = "" + this.stakeAmount;
        if (str.indexOf(".") !== -1) {
          let arr = str.split("");
          arr.splice(arr.length - 1);
          let str2 = arr.join("");
          this.stakeAmount = +str2;
        }
      } else {
        let str = "" + this.withdrawAmount;
        if (str.indexOf(".") !== -1) {
          let arr = str.split("");
          arr.splice(arr.length - 1);
          let str2 = arr.join("");
          this.withdrawAmount = +str2;
        }
      }
    },
    protectAddress: function (addr, num) {
      if (addr) {
        var newAddr = addr.replace('"', "").replace('"', "");
        if (newAddr == "0x0000000000000000000000000000000000000000") {
          return "--";
        }
        if (!newAddr) {
          return "--";
        }
        if (newAddr == 1) {
          return newAddr;
        }
        return (newAddr + "").length == 42
          ? newAddr.substring(0, num) + "****" + newAddr.substring(39, 42)
          : newAddr;
      }
    },
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    // 复制成功时的回调函数
    onCopy(e) {
      var txt = this.$t("message.copySuccee");
      this.$toast.success(txt);
    },
    // 复制失败时的回调函数
    onError(e) {
      var txt = this.$t("message.copyFail");
      this.$toast.fail(txt);
    },
    ceilNum(d, prex = 4) {
      if (parseInt(d) == d) return d;
      let len = 1;
      let i = 0;
      while (parseInt(d) != d) {
        d = d * 10;
        len *= 10;
        i++;
      }
      let last = Math.abs(i <= prex ? 0 : d % Math.pow(10, i - prex));
      let d1 = d - last;
      if (last > 0) {
        d1 = d1 + Math.pow(10, i - prex);
      }
      d1 = d1 / len;
      return d1;
    },
    getNbn(e, type) {
      if (type == 1) {
        var val = e;
      } else {
        var val = e.target.value;
      }
      if (!e) return;
      if (this.cionType == "five") {
        var Price2 = this.hmdInfo.fivePrice;
      } else {
        var Price2 = this.hmdInfo.GrccPrice;
      }
      var prex = 4;
      var price = Number(Price2) * Number(val);

      // info.fivePrice =null;
      if (parseInt(price) == price) {
        this.nbn = price;
        return;
      }
      let len = 1;
      let i = 0;
      while (parseInt(price) != price) {
        price = price * 10;
        len *= 10;
        i++;
      }
      let last = Math.abs(i <= prex ? 0 : price % Math.pow(10, i - prex));
      let d1 = price - last;
      if (last > 0) {
        d1 = d1 + Math.pow(10, i - prex);
      }
      d1 = d1 / len;
      this.nbn = d1;
    },
    //取消抵押
    withdraw: function () {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      var myzy = web3.utils.fromWei(_this.hmdInfo.info[19], "ether");
      if (myzy <= 0) {
        _this.$toast(_this.$t("message.noZy"));
        return;
      }
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.mxtContract.methods
        .exit()
        .send({ from: account })
        .then((res) => {
          console.log("withdraw", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee"));
          _this.add_hmdInfo();
          _this.getCoinBalance();
          _this.add_hmdCoinContract();
          // }

          _this.withdrawAmount = "";
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    max: function () {
      var num = parseInt(this.hmdBalance / 10**18);
      var prec = 3;
      var ceil = true;
      console.log(num)
      // num = num.toString();
      // let index = num.indexOf(".");
      // if (index !== -1) {
      //   num = num.substring(0, 6 + index + 1);
      // } else {
      //   num = num.substring(0);
      // }
      const len = String(num).length;
      if (len <= prec) {
        return num;
      }

      const mult = Math.pow(10, prec);
      this.stakeAmount = Math.floor(num / mult) * mult;

      // this.stakeAmount = num;
  // this.getNbn(this.stakeAmount, 1);
    },
    stake: function () {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var inviteCode = _this.hmdInfo.inviteCode.toString();
      var referrer = _this.hmdInfo.referrer.toString();
      var account = _this.account;

      // if (!_this.isApprove) {
      //   _this.$toast(_this.$t("message.noAccredit"));
      //   return;
      // }

      if (!referrer || referrer == "--") {
        if( _this.$route.query.invite){
          referrer = _this.$route.query.invite;
        }else if( localStorage.getItem("invite")){
          referrer = localStorage.getItem("invite")
        }else{
           referrer = 1;
        }
      }
      if (!inviteCode || inviteCode == "--") {
        inviteCode = _this.account;
      }

      if (inviteCode == referrer) {
        referrer = "1";
      }
      if (Number(_this.stakeAmount) % 1000 > 0) {
        _this.$toast(_this.$t("message.yu"));
        return;
      }
      if (Number(_this.stakeAmount) > Number(_this.hmdBalance)) {
        _this.$toast(_this.$t("message.noBanner"));
        return;
      }
      if (_this.balance < 0.01) {
        _this.$toast(_this.$t("message.gas"));
        return;
      }
      if (Number(_this.stakeAmount) <= 0) {
        _this.$toast(_this.$t("message.noNum"));
        return;
      }
      if (Number(_this.stakeAmount) < 100) {
        _this.$toast(_this.$t("message.f_67"));
        return;
      }
      if (!referrer || referrer == "--") {
        _this.$toast(_this.$t("message.onReferees"));
        return;
      }
      var monye = (_this.stakeAmount * 10 ** 18).toString();
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      var tokenArr =
        _this.cionType == "five" ? _this.ArrList.FiveArr : _this.ArrList.GtArr;
      console.log(
        "investIn=======",
        inviteCode,
        referrer,
        web3.utils.toWei(_this.stakeAmount.toString(), "ether")
      );
      this.mxtContract.methods
        .investIn(
          inviteCode,
          referrer,
          web3.utils.toWei(_this.stakeAmount.toString(), "ether")
        )
        .send({
          from: account,
        })
        .then((res) => {
          console.log("investIn", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee"));
          _this.add_hmdInfo();
          _this.getCoinBalance();
          _this.add_hmdCoinContract();
          //   setTimeout(function(){
          //      _this.add_hmdInfo();
          //     _this.getCoinBalance();
          //     _this.add_hmdCoinContract();
          //   },10000)
          // }

          _this.stakeAmount = "";
          _this.nbn = "";
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);

      // this.$router.push({
      //   name: this.$route.name,
      //   params: { lang: this.lang },
      //   query: { r: localStorage.getItem("r") },
      // });
    },
    // GRCC授权
    GrccApprove: function () {
      if (this.GCoinContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = this.hmdInfo.address;
      var account = this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.GCoinContract.methods
        .approve(address, "10000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("approve+GrccApprove", res);
          this.$toast.clear();
          this.$toast.success(this.$t("message.accredit"));
          this.isApproveGrcc = true;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    // Five授权
    FiveApprove: function () {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = this.hmdInfo.address;
      var account = this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.FiveCoinContract.methods
        .approve(address, "5000000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("approve", res);
          this.$toast.clear();
          this.$toast.success(this.$t("message.accredit"));
          this.isFiveApprove = true;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    // 是否授权
    FiveisApproveFun: function () {
      var account = this.account;
      var address = this.hmdInfo.address;

      this.FiveCoinContract.methods
        .allowance(account, address)
        .call()
        .then((res) => {
          var a = web3.utils.fromWei(res, "ether");
          if (a > 0) {
            this.isFiveApprove = true;
          }
        });
    },

    // USDT授权
    lpApprove: function () {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = this.hmdInfo.address;
      var account = this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.mxtCoinContract.methods
        .approve(address, "5000000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("approve", res);
          this.$toast.clear();
          this.$toast.success(this.$t("message.accredit"));
          this.isApprove = true;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    getUrl: function () {
      var hash = window.location.hash;
      if (hash.indexOf("?") != -1) {
        hash = hash.split("?")[0];
      }
      var url = window.location.origin + window.location.pathname + hash;
      var newAddr = "";
      if (this.hmdInfo) {
        if (this.hmdInfo.referrer) {
          var addr = this.hmdInfo.inviteCode;
          newAddr = addr.replace('"', "").replace('"', "");
          this.link = url + "?r=" + newAddr;
        } else {
          this.link = url;
        }
      }
      return this.link;
    },
    // 是否授权
    isApproveFun: function () {
      var account = this.account;
      var address = this.hmdInfo.address;

      this.mxtCoinContract.methods
        .allowance(account, address)
        .call()
        .then((res) => {
          console.log("usdt=========isApproveFun", account,address);
          var a = web3.utils.fromWei(res, "ether");
          if (a > 0) {
            this.isApprove = true;
          }
        });
    },
    // GRCC是否授权
    GrccisApproveFun: function () {
      var account = this.account;
      var address = this.hmdInfo.address;

      this.GCoinContract.methods
        .allowance(account, address)
        .call()
        .then((res) => {
          console.log("=========GCoinContract", res);
          var a = web3.utils.fromWei(res, "ether");
          if (a > 0) {
            this.isApproveGrcc = true;
          }
        });
    },
    //获取当前币种余额
    getCoinBalance: function () {
      if (this.mxtCoinContract) {
        var account = this.account;
        this.mxtCoinContract.methods
          .balanceOf(account)
          .call()
          .then((res) => {
            var num = web3.utils.fromWei(res, "ether");
            num = num.toString();
            let index = num.indexOf(".");
            if (index !== -1) {
              num = num.substring(0, 6 + index + 1);
            } else {
              num = num.substring(0);
            }
            this.coinBalance = res;
          });
      }
    },
    get_param: function (name) {
      var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
      var r = window.location.search.substr(1).match(reg);
      if (r != null) {
        return unescape(r[2]);
      }
      // console.log(r)
      return null;
    },
  },
  created() {
    this.add_mxtCoinContract();
    this.getCoinBalance();
  },
  mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "en");
    if (this.$route.query.invite) {
      localStorage.setItem("invite", this.$route.query.invite);
    }
    var getData = () => {
      this.add_hmdInfo();
      this.add_mxtCoinContract();
      this.add_hmdCoinContract();
      this.add_gCoinContract();
      this.add_aesCoinContract();
    };
    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.hmdInfo || !this.mxtCoinContract || !this.hmdBalance) {
          getData();
        } else {
          this.getCoinBalance();
          this.isApproveFun();
          this.FiveisApproveFun();
          this.GrccisApproveFun();
           if (_this.hmdInfo.inviteCode || _this.hmdInfo.referrer) {
            localStorage.setItem("myLink", _this.getUrl());
          }
          clearInterval(this.timer);
        }
      }, 1000);
    };
    getDataCalculate();

    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 80000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
};
</script>

