                  />
                  <span
                    style="font-size: 14px; color: #62e6f3; font-weight: 800"
                    >FLW</span
                  >
                </div>
               
              </div>
            </van-col>
            <van-col span="12" 
              ><p class="rightTxt">
                <span>{{ $t("message.f_8") }}</span>
                {{ hmdBalance | figure }}
              </p></van-col
            >
          </van-row>
          <div class="put">
            <van-row type="flex" justify="space-between" gutter="10">
              <van-col span="24" class="topPledge">
                <input type="number"
                  placeholder="0"
