                  onkeyup="this.value=this.value.replace(/[^0-9]/g,'')"
                />
              </van-col>
              <!-- <van-col span="6" class="textRight"
                >
                <button >MAX</button>
                </van-col> -->
            </van-row>
          </div>
        </div>
               
