       <button class="btn" @click="stake" v-if="isFiveApprove">
                  {{ $t("message.f_10") }}
                </button>
                <button class="btn" @click="FiveApprove" v-else>
                  {{ $t("message.f_10_1") }} FLW
                </button>
      </div>
     

         <ul>
             <li>
            {{$t("message.m_41")}}<span>{{ NFTInfo ? (1000-Number(NFTInfo.info[8])) : "--" }} {{$t("message.m_41_1")}}</span
            >
          </li>
            <li>
            {{$t("message.m_42")}}<span
              >{{ NFTInfo ? NFTInfo.info[1] : "0" | figureK }} USDT</span
            >
          </li>
           <li>
            {{$t("message.m_43")}}<span
              >{{ NFTInfo ? NFTInfo.info[2] : "0" | figureK}} FLW</span
            >
          </li>
          <li>
            {{$t("message.m_44")}}<span
              >{{ NFTInfo ? NFTInfo.info[4] : "0" | figureK }} USDT</span
            >
          </li>
          <li>
            {{$t("message.m_45")}}<span
              >{{
                NFTInfo
                  ?(Number(NFTInfo.info[1] )*5-Number(NFTInfo.info[4]))
                  : "0" | figureK
              }}
              USDT</span
            >
          </li>
          <li>
            {{$t("message.m_46")}}<span
              >{{
                NFTInfo
                  ?(Number(NFTInfo.info[6] ))
                  : "0" | figureK
              }}
              USDT</span
            >
          </li>
        </ul>
           <van-row type="flex" justify="space-between" gutter="20">
              <van-col span="12" class="textCenter">
                <button class="btn1" @click="redeemFun">{{$t("message.m_47")}}</button>
              </van-col>
              <van-col span="12" class="textCenter">
                 <button class="btn1" @click="withdrawProfitFun">{{$t("message.m_47_1")}}</button>
              </van-col>
           </van-row>
      </div>
    </div>

    <div class="rult">
      <h1>{{$t("message.m_48")}}</h1>
      <ul>
        <li>{{$t("message.m_49")}}</li>
<li>{{$t("message.m_50")}}</li>
<li>{{$t("message.m_51")}}</li>
<li>{{$t("message.m_52")}}</li>
      </ul>
    </div>

  </div>
</template>

<script type="es6">
import HeadTop from "../../components/Header";
import { mapActions, mapState } from "vuex";
export default {
  data() {
    return {
      lang: "",
        tab: "1",
      link: "--",
        coinBalance: 0,
        isApprove: false,
        isFiveApprove:false,
        stakeAmount: 2000,
      //   withdrawAmount: 0,
      timer: null,
      timer2: null,
      TokenPrice: 0,
      tabsNum: 1,
    };
  },
  computed: {
    ...mapState("main", ["NFTContract",  "account",'NFTInfo','usdtCoinContract',
    "hmdBalance","FiveCoinContract",
    ]),
  },
  methods: {
    ...mapActions("main", [
      "add_NFTInfo",
      "add_usdtCoinContract",
      "add_hmdCoinContract",
    ]),
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
     changeTab(num) {
      this.tab = num;
    },
    hreftwo(url, title) {
      if (url.length > 2) {
        this.$router.push({ path: url });
      } else {
        this.$router.push({ path: url });
      }

    },
    withdrawProfitFun: function () {
      if (this.NFTContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.NFTContract.methods
        .withdrawProfit()
        .send({ from: account })
        .then((res) => {
          console.log("withdrawProfit", res);
          // if (res.status) {
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
          _this.add_NFTInfo();
          _this.getCoinBalance();
          // }
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    redeemFun: function () {
      if (this.NFTContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var monye = Number(web3.utils.fromWei(this.NFTInfo.info[1], 'ether'))*5;
      var monye2 = Number(web3.utils.fromWei(this.NFTInfo.info[4], 'ether'));
      console.log(monye,monye2,monye>monye2);
        if (monye>monye2) {
        this.$toast(this.$t("message.m_53"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.NFTContract.methods
        .redeem()
        .send({ from: account })
        .then((res) => {
          console.log("redeem", res);
          // if (res.status) {
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
          _this.add_NFTInfo();
          _this.getCoinBalance();
          // }
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
 stake: function () {
      if (this.NFTContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;

      if (_this.NFTInfo.info[7]=='0') {
        _this.$toast(_this.$t("message.m_54"));
        return;
      }

      // if ( Number(_this.coinBalance)<1000) {
      //   _this.$toast('USDT'+_this.$t("message.noBanner"));
      //   return;
      // }
       // if (web3.utils.toWei(_this.hmdBalance, 'ether')<10000) {
      //   _this.$toast('FLW'+_this.$t("message.noBanner"));
      //   return;
      // }
      if (_this.balance < 0.01) {
        _this.$toast(_this.$t("message.gas"));
        return;
      }
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.NFTContract.methods
        .investIn().send({
          from: account,
        })
        .then((res) => {
          console.log("investIn", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee"));
          _this.add_NFTInfo();
          _this.getCoinBalance();
          // _this.add_hmdCoinContract();
          //   setTimeout(function(){
          //      _this.add_NFTInfo();
          //     _this.getCoinBalance();
          //     _this.add_hmdCoinContract();
          //   },10000)
          // }

          _this.stakeAmount = "";
          _this.nbn = "";
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
       // USDT授权
    lpApprove: function () {
      if (this.NFTContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = this.NFTInfo.address;
      var account = this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.usdtCoinContract.methods
        .approve(address, "5000000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("approve", res);
          this.$toast.clear();
          this.$toast.success(this.$t("message.accredit"));
          this.isApprove = true;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
     // 是否授权
    isApproveFun: function () {
      var account = this.account;
      var address = this.NFTInfo.address;

      this.usdtCoinContract.methods
        .allowance(account, address)
        .call()
        .then((res) => {
          console.log("usdt=========isApproveFun", account,address);
          var a = web3.utils.fromWei(res, "ether");
          if (a > 0) {
            this.isApprove = true;
          }
        });
    },
    // FLW授权
    FiveApprove: function () {
      if (this.FiveCoinContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = this.NFTInfo.address;
      var account = this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.FiveCoinContract.methods
        .approve(address, "5000000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("approve", res);
          this.$toast.clear();
          this.$toast.success(this.$t("message.accredit"));
          this.isFiveApprove = true;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    // FLW是否授权
    FiveisApproveFun: function () {
      var account = this.account;
      var address = this.NFTInfo.address;

      this.FiveCoinContract.methods
        .allowance(account, address)
        .call()
        .then((res) => {
          var a = web3.utils.fromWei(res, "ether");
          if (a > 0) {
            this.isFiveApprove = true;
          }
        });
    },
        //获取当前币种余额
    getCoinBalance: function () {
      if (this.usdtCoinContract) {
        var account = this.account;
        this.usdtCoinContract.methods
          .balanceOf(account)
          .call()
          .then((res) => {
            var num = web3.utils.fromWei(res, "ether");
            num = num.toString();
            let index = num.indexOf(".");
            if (index !== -1) {
              num = num.substring(0, 6 + index + 1);
            } else {
              num = num.substring(0);
            }
            this.coinBalance = res;
          });
      }
    },
    getUrl: function (addr) {
      var hash = window.location.hash;
      if (hash.indexOf("?") != -1) {
        hash = hash.split("?")[0];
      }
      var url = window.location.origin + window.location.pathname + "#/";
      var newAddr = "";
      if (addr) {
        newAddr = addr.replace('"', "").replace('"', "");
        this.link = url + "?invite=" + newAddr;
      }
      return this.link;
    },
    protectAddress: function (addr, num) {
      if (addr == "0x0000000000000000000000000000000000000000") {
        return "--";
      }
      if (!addr) {
        return "--";
      }
      return (addr + "").length == 42
        ? addr.substring(0, num) + "****" + addr.substring(38, 42)
        : addr;
    },
  },
  mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "zhCHS");
    if (this.$route.query.r) {
      localStorage.setItem("r", this.$route.query.r);
    }



    
    var getData = () => {
      this.add_NFTInfo();
      this.add_usdtCoinContract();
      this.add_hmdCoinContract();
    };
    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.NFTInfo || !this.usdtCoinContract ) {
          getData();
        } else {
          _this.getCoinBalance();
          _this.isApproveFun();
          _this.FiveisApproveFun();
          clearInterval(this.timer);
        }
      }, 1000);
    };

    getDataCalculate();
    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    },80000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
  components: {
    HeadTop,
  },
};
</script>

