  }
  .top-3{
    position: absolute;
        top: -40px;
    right: 90px;
       -webkit-animation: jump 3s ease infinite;
  animation: jump 3s ease infinite;
  }
  .top-4{
    position: absolute;
        top: 60px;
    right:35px;
    -webkit-animation: jump2 5s ease infinite;
  animation: jump2 5s ease infinite;
  }
  .top-5{
    position: absolute;
   top: 180px;
    right: 95px;
    -webkit-animation: jump 3s ease infinite;
  animation: jump 3s ease infinite;
  }
}
@keyframes jump {
  0% {
    transform: translateY(0) scale(1, 1);
  }
  /* 中间状态图片位移并且拉伸 */
  50% {
    transform: translateY(-10px) scale(0.95, 1.03);
  }
  100% {
    transform: translateY(0) scale(1, 1);
  }
}
@keyframes jump2 {
  0% {
    transform: translateX(0) scale(1, 1);
  }
  /* 中间状态图片位移并且拉伸 */
  50% {
    transform: translateX(-10px) scale(0.95, 1.03);
  }
  100% {
    transform: translateX(0) scale(1, 1);
  }
}
