<template>
  <div class="info">
    <HeadTop :lang="lang" @changeAppLocale="changeAppLocale" :titleHead="this.$t('message.f_61')"/>
       
    <div class="top">
      <van-row gutter="20" type="flex" justify="space-between">
        <van-col span="24" class="textRight">
          <h2>{{hmdInfo ? (hmdInfo.info[8]) : "0"  | figureK  }} FLW</h2>
        </van-col>
      </van-row>
       <van-row gutter="20" type="flex" justify="space-between">
        <van-col span="24" class="textRight">
          <span>{{ $t("message.f_a_8") }}</span>
        </van-col>
      </van-row>
    </div>

       <h1 class="textCenter title">
        <span>{{ $t("message.f_a_9") }}</span>
      </h1>
      <p class="tit-p">{{ $t("message.f_a_10") }}</p>
    <div class="cent">
     
      <div class="">
        <ul>
          <li>{{ $t("message.f_55") }}<span>{{hmdInfo ? (hmdInfo.info[14]) : "0"   }} {{ $t("message.f_57") }}</span></li>
           <li>{{ $t("message.f_52") }}<span>{{hmdInfo ? (hmdInfo.info[15] / 1000) : "0"  | figureK  }} H/s</span></li>
          <li>{{ $t("message.f_53") }}<span>{{hmdInfo ? (hmdInfo.info[11]) : "0"   }} {{ $t("message.f_57_1") }}</span></li>
          <li>{{ $t("message.f_54") }}<span>{{hmdInfo ? (hmdInfo.info[13] / 1000) : "0"  | figureK  }} H/s</span></li>
          <li>{{ $t("message.f_56") }}<span>{{
            this.hmdInfo ? protectAddress(hmdInfo.referrer, 8) : "--"
          }}</span></li>
        </ul>
      </div>
    </div>

    
  </div>
</template>

<script type="es6">
import HeadTop from "../../components/Header";
import { mapActions, mapState } from "vuex";

export default {
  data() {
    return {
      lang: "",
      //   currency: "",
      link: "--",
      //   coinBalance: 0,
      //   isApprove: false,
      //   stakeAmount: 1,
      //   withdrawAmount: 0,
      timer: null,
      timer2: null,
      TokenPrice: 0,
      tabsNum: 1,
      F5data: {
        allBonus: 0,
        balanceBonus: 0,
        vipLength: 0,
      },
    };
  },
  computed: {
    ...mapState("main", ["hmdInfo", "mxtContract", "account", "aesBalance"]),
  },
  methods: {
    ...mapActions("main", [
      "add_hmdInfo",
      "add_mxtCoinContract",
      "add_aesCoinContract",
    ]),
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
    getUsdt(num) {
      return num * this.hmdInfo.fivePrice;
    },
    // 复制成功时的回调函数
    onCopy(e) {
      var txt = this.$t("message.copySuccee");
      this.$toast.success(txt);
    },
    // 复制失败时的回调函数
    onError(e) {
      var txt = this.$t("message.copyFail");
      this.$toast.fail(txt);
    },
    getUrl: function (addr) {
      var hash = window.location.hash;
      if (hash.indexOf("?") != -1) {
        hash = hash.split("?")[0];
      }
      var url = window.location.origin + window.location.pathname + "#/";
      var newAddr = "";
      if (addr) {
        newAddr = addr.replace('"', "").replace('"', "");
        this.link = url + "?invite=" + newAddr;
      }
      return this.link;
    },
    protectAddress: function (addr, num) {
      if (addr == "0x0000000000000000000000000000000000000000") {
        return "--";
      }
      if (!addr) {
        return "--";
      }
      return (addr + "").length == 42
        ? addr.substring(0, num) + "****" + addr.substring(38, 42)
        : addr;
    },

  },
  mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "zhCHS");
    this.currency = "HMD";
    if (this.$route.query.r) {
      localStorage.setItem("r", this.$route.query.r);
    }
    var getData = () => {
      this.add_hmdInfo();
      this.add_mxtCoinContract();
      this.add_aesCoinContract();
    };
    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.hmdInfo || !this.mxtContract) {
          getData();
        } else {
          _this.dataInfo = _this.hmdInfo.info;
          // _this.getPrice();
          if (_this.hmdInfo.inviteCode || _this.hmdInfo.referrer) {
            var invite = _this.hmdInfo.inviteCode || _this.hmdInfo.referrer;
            _this.getUrl(invite);
          }
          clearInterval(this.timer);
        }
      }, 1000);
    };

    getDataCalculate();
    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 10000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
  components: {
    HeadTop,
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped  lang="less">
.info {
  background:url(../../assets/img/flw/bottom.png) bottom center no-repeat #000000;
   background-size: 100%;
  padding: 5.5em 0 3em;
  min-height: 100%;
    box-sizing: border-box;
  .top {
    text-align: center;
    padding: 21px;
    width: 345px;
    // height: 188px;
    box-sizing: border-box;
    background: url(../../assets/img/flw/ming.png) top center no-repeat;
    background-size: 100%;
    margin: 5px auto 20px;
    box-sizing: border-box;
    .sl {
      margin-bottom: 10px;
    }
    span {
      color: #fff;
      // font-weight: bold;
      font-family: PingFangSC-Regular, PingFang SC;
      font-size: 14px;
      text-align: center;
      display: block;
      margin-top: 5px;
    }
    h3{
      font-size: 13px;
      text-align: center;
      font-weight: normal;
      margin-top: 35px;
      img{
        vertical-align: middle;
      }
    }
    h2 {
      font-size: 22px;
      margin-top: 13px;
      margin-bottom: 30px;
      font-family: DINAlternate-Bold, DINAlternate;
      font-weight: bold;
      text-align: center;
      color: #ffffff;
    }
  }


  .ming_bix {
    background:transparent;
    border-radius: 10px;
    overflow: hidden;
    box-sizing: border-box;
    width: 90%;
    margin: 0 auto 30px;
    margin-top: 20px;
    .list{
        background:#1E3756;
        padding: 20px 10px;
        div.van-row{
              border-bottom: 1px solid #1a2e46;
    padding: 12px 0;
        }
        span{
          color: #B57EFF;
        }
        .textRight span{
          color: #fff;
        }
        
    }
    .tab {
      div {
        padding: 10px 0 10px;
        background:#102033;
        border-radius: 10px 10px 0 0;
      
        span {
          color: #5C80AC;
          font-size: 15px;
        }
      }
      .in {
        background: url(../../assets/img/flw/dd.png) top center no-repeat #1e3756;
        background-size: 200%;
        border-radius: 10px 10px 0 0;
        span {
          color: #fff;
          font-weight: bold;
        }
      }
    }
  }
.title span::before {
  content: "";
  display: block;
  position: absolute;
  width: 44px;
  height: 10px;
  background: url(../../assets/img/flw/left3.png) top center no-repeat ;
  background-size: 100%;
  top: 10px;
  left: -60px;
}
   .title span::after {
      content: "";
      display: block;
      position: absolute;
      width: 36px;
      height: 10px;
      background: url(../../assets/img/flw/right3.png) top center no-repeat;
      background-size: 100%;
      top: 10px;
      right: -50px;
    }
    .title {
      text-align: center;
      margin: 30px 0 0px;
      span {
        position: relative;
        font-size:20px;
        font-family: SourceHanSansCN-Bold, SourceHanSansCN;
        font-weight: bold;
        color: #fff;
        line-height: 39px;
        text-align: left;
        text-shadow: 4px 4px 10px  #103b7e;
      }
    }

  .cent {
    background: url(../../assets/img/flw/c.png) top center no-repeat #1e3756;
    background-size: 100%;
    padding: 20px;
    width: 90%;
    margin: 0 auto 30px;
    position: relative;
    box-sizing: border-box;
    border-radius: 10px;
   
    span {
      font-size: 12px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: #aba8a8;
    }
      li::after {
      content: "";
      display: block;
      position: absolute;
      width: 12px;
      height: 12px;
      background: url(../../assets/img/flw/dian.png) top center no-repeat;
      background-size: 100%;
      top: 15px;
      left: -25px;
    }
    li{
      position: relative;
      margin-left: 20px;
      padding: 15px 0;
      border-bottom:1px solid rgb(26 46 70);
      color: #62E6F3;
      span{font-size: 15px;
        float: right;
        color: #FFFFFF;
        max-width: 60%;
        text-align: right;
       overflow: hidden; /*溢出隐藏*/
      white-space: nowrap; /*规定文本不进行换行*/
      text-overflow: ellipsis;
        
      }
    }
    .btn{
      margin: 30px auto 20px;
      display: block;
 
    }
       a.btn{color: #fff;}
    // h3 {
    //   font-size: 16px;
    //   font-family: PingFangSC-Medium, PingFang SC;
    //   font-weight: 500;
    //   color: #323335;
    //   margin-bottom: 15px;
    //   font-weight: bold;
    //   margin-top: 5px;
    //   overflow: hidden; /*溢出隐藏*/
    //   white-space: nowrap; /*规定文本不进行换行*/
    //   text-overflow: ellipsis;
    //   width: 100%;
    //   margin-right: 5px;
    // }
  }
}
.btn {
  background: url(../../assets/img/flw/44414141.png) top center no-repeat;
  background-size: 100%;
  border: none;
  width: 120px;
  text-align: center;
  // font-size: 16px;
  // font-weight: bold;
  height: 42px;
  line-height: 42px;
  margin-top: 5px;
}
.tit-p{
      font-size: 13px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    line-height: 22px;
    color: #eee;
    text-align: center;
    margin:0 20px 30px;
}
</style>



