      <div class="buy" v-if="tab == '1'">
        <div class="box">
          <van-row gutter="20" type="flex" justify="space-between">
            <van-col span="12" class="">
              <h2>
                {{$t("message.m_8")}}{{ magicInfo ? protectAddress(account, 2) : "--" }}
              </h2>
            </van-col>
