            </van-col>
          </van-row>
        </div>
        <button v-if="isApprove" @click="stake">
          {{$t("message.m_10")}}
          <span>（{{$t("message.m_11")}}{{ magicInfo ? 10 - magicInfo.info[25] : "0" }}{{$t("message.m_12")}}）</span>
        </button>
        <button v-else @click="lpApprove">
          {{$t("message.f_10_1")}}USDT
          <span>（{{$t("message.m_11")}}{{ magicInfo ? 10 - magicInfo.info[25] : "0" }}{{$t("message.m_12")}}）</span>
        </button>
      </div>
    </div>

    <div class="tab">
