          @click="changeTab('2')"
        >
          <h4>{{$t("message.m_14")}}</h4>
        </van-col>
      </van-row>
    </div>

    <div class="cent" v-if="tab == '1'">
      <div v-if="magicInfo && magicInfo.nums.length > 0">
        <div class="minTit"><span>{{$t("message.m_15")}}</span></div>
        <div class="list">
          <van-row gutter="10" type="flex" justify="start">
            <van-col
              span="8"
              class="textRight"
              v-for="(itme, index) in magicInfo.nums"
              :key="index"
            >
              <div class="myBox">
                <img
                  src="../../assets/img/magic/box-2.png"
                  alt=""
                  width="100%"
                />
                <span>{{$t("message.m_16")}}{{ itme }}</span>
              </div>
            </van-col>
          </van-row>
        </div>
      </div>

      <div class="sy">
        <span class="title">{{$t("message.m_17")}}</span>
        <van-row gutter="10" type="flex" justify="space-between">
          <van-col span="16" class="text">
            {{
              magicInfo
                ? Number(magicInfo.info[13]) +
                  Number(magicInfo.info[15]) +
                  Number(magicInfo.info[17])
                : "0" | figureK
            }}</van-col
          >
          <van-col span="8" class="textRight"
            ><button @click="withdrawUsdtFun">{{$t("message.m_18")}}USDT</button></van-col
          >
        </van-row>
      </div>
      <div class="myInfo">
        <div class="top">
          <van-row
            gutter="20"
            type="flex"
            justify="space-between"
            class="topHead"
          >
            <van-col span="12">
              <h3>{{$t("message.m_19")}}</h3>
            </van-col>
            <van-col span="12" class="textRight">
              <h1 class="textRight">
                {{ magicInfo ? magicInfo.info[14] : "0" | figureK }} USDT
              </h1>
            </van-col>
          </van-row>
          <van-row gutter="20" type="flex" justify="space-between">
            <van-col span="12" class="textCenter">
              <div class="txt">
                <h3>{{$t("message.m_20")}}(USDT)</h3>
                <h2>{{ magicInfo ? magicInfo.info[16] : "0" | figureK }}</h2>
              </div>
            </van-col>
            <van-col span="12" class="textCenter">
              <div class="txt">
                <h3>{{$t("message.m_21")}}(USDT)</h3>
                <h2>{{ magicInfo ? magicInfo.info[18] : "0" | figureK }}</h2>
              </div>
            </van-col>
          </van-row>
        </div>
        <ul>
          <!-- <li>
            {{$t("message.m_22")}}<span
              >{{ magicInfo ? magicInfo.info[9] : "0" }} {{$t("message.m_31")}}</span
            >
          </li> -->
          <li>
            {{$t("message.m_23")}}<span
              >{{ magicInfo ? magicInfo.info[11] : "0" | figureK }} USDT</span
            >
          </li>
          <li>
            {{$t("message.m_24")}}<span
              >{{
                magicInfo
                  ? Number(magicInfo.info[14]) +
                    Number(magicInfo.info[16]) +
                    Number(magicInfo.info[18])
                  : "0" | figureK
              }}
              USDT</span
            >
          </li>
          <li>
            {{$t("message.m_25")}}<span
              >{{ magicInfo ? magicInfo.info[20] : "0" | figureK }} USDT</span
            >
          </li>
          <li>
            {{$t("message.m_26")}}<span>{{ magicInfo ? magicInfo.info[19] : "0" }} {{$t("message.m_31")}}</span>
          </li>
          <li>
            {{$t("message.m_27")}}<span
              >{{ magicInfo ? magicInfo.info[22] : "0" | figureK }} USDT</span
            >
          </li>
          <li>
             {{$t("message.m_28")}}<span>{{ magicInfo ? magicInfo.info[21] : "0" }}  {{$t("message.m_31")}}</span>
          </li>
          <!-- <li>
             {{$t("message.m_29")}}<span>{{ magicInfo ? Number(magicInfo.info[24]) : "0" }}  {{$t("message.m_30")}}</span>
          </li> -->
          <li>
             {{$t("message.m_32")}}<span>{{
              magicInfo ? protectAddress(magicInfo.referrer, 4) : "--"
            }}</span>
          </li>
          <li>
             {{$t("message.m_33")}}<span>{{ this.link }}</span>
          </li>
        </ul>
        <button
          class="btn"
          v-clipboard:copy="link"
          v-clipboard:success="onCopy"
          v-clipboard:error="onError"
        >
          {{$t("message.m_34")}}
        </button>
      </div>
    </div>

    <div class="cent" v-if="tab == '2'">
      <div class="sy">
        <span class="title">{{$t("message.m_35")}} </span>
        <van-row gutter="10" type="flex" justify="space-between">
          <van-col span="24" class="text">
            {{
              magicInfo ? protectAddress(magicInfo.rLastWinner, 8) : "--"
            }}</van-col
          >
          <!-- <van-col span="24" class="text"> {{ magicInfo ? protectAddress(magicInfo.rLastWinner ,8) : "--" }}</van-col> -->
        </van-row>
      </div>
      <div class="myInfo">
        <div class="top">
          <van-row
            gutter="20"
            type="flex"
            justify="space-between"
            class="topHead"
          >
            <van-col span="24">
              <h3>{{$t("message.m_36")}}</h3>
            </van-col>
            <!-- <van-col span="12" class="textRight">
              <h1 class="textRight">$ {{ magicInfo ? magicInfo.info[27] : "0" | figureK }}</h1>
            </van-col> -->
          </van-row>
          <van-row gutter="20" type="flex" justify="space-between">
            <van-col span="12" class="textCenter">
              <div class="txt">
                <h3>{{$t("message.m_37")}}(USDT)</h3>
                <h2>{{ magicInfo ? magicInfo.info[27] : "0" | figureK }}</h2>
              </div>
            </van-col>
            <van-col span="12" class="textCenter">
              <div class="txt">
                <h3>{{$t("message.m_38")}}</h3>
                <h2>
                  {{ magicInfo ? magicInfo.info[26] : "0" }}
                </h2>
              </div>
            </van-col>
          </van-row>
        </div>
        <ul v-if="magicInfo && magicInfo.tema.length > 0">
          <li v-for="(itme, index) in magicInfo.tema" :key="index" >
            {{$t("message.m_39")}}<span>{{ protectAddress(itme, 8) }}</span>
          </li>
        </ul>
      </div>
    </div>
    <van-overlay :show="show" :z-index="13">
      <div class="tips">
        <p> {{$t("message.m_56")}}</p>
         <van-row gutter="20" type="flex" justify="space-between">
            <van-col span="12" class="textRight">
              <button class="btn" @click="clos"> {{$t("message.m_57")}}</button>
            </van-col>
            <van-col span="12" class="textLeft">
              <button class="btn" @click="dataSynFun"> {{$t("message.f_10")}}</button>
            </van-col>
          </van-row>
        
      </div>
    </van-overlay>
    <!-- <van-popup
      v-model="show"
      position="top"
      :style="{ height: 'auto', top: '30%' }"
      :close-on-click-overlay="false"
    >
      <div class="tips">
          <p>本期盲盒已售罄，请立即开奖！</p>
          <button class="btn" >开 奖</button>
        </div>
    </van-popup> -->
  </div>
</template>

<script type="es6">
import HeadTop from "../../components/Header";
import { mapActions, mapState } from "vuex";

export default {
  data() {
    return {
      lang: "",
      tab: "1",
      link: "--",
      coinBalance: 0,
      isApprove: false,
      //   stakeAmount: 1,
      //   withdrawAmount: 0,
      timer: null,
      arr: [],
      timer2: null,
      TokenPrice: 0,
      tabsNum: 1,
      show: false,
    };
  },
  computed: {
    ...mapState("main", [
      "magicContract",
      "account",
      "magicInfo",
      "usdtCoinContract",
      "hmdInfo",
    ]),
  },
  methods: {
    ...mapActions("main", [
      "add_magicInfo",
      "add_usdtCoinContract",
      "add_hmdInfo",
    ]),
    clos(){
      this.show=false;
    },
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
    changeTab(num) {
      var _this = this;
          if(num=='2'&& _this.magicInfo.info[0]=='0'){
        this.$toast(_this.$t("message.m_55"));
        return
      }
      _this.tab = num;

      setTimeout(function () {
        _this.initContent();
      }, 2000);
    },

    getUsdt(num) {
      return num * this.magicInfo.fivePrice;
    },
    // 复制成功时的回调函数
    onCopy(e) {
      var txt = this.$t("message.copySuccee");
      this.$toast.success(txt);
    },
    // 复制失败时的回调函数
    onError(e) {
      var txt = this.$t("message.copyFail");
      this.$toast.fail(txt);
    },
    hreftwo(url, title) {
      if (url.length > 2) {
        this.$router.push({ path: url });
      } else {
        this.$router.push({ path: url });
      }
    },
    dataSynFun: function () {
      if (this.hmdInfo == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      //  this.$toast(_this.hmdInfo.inviteCode,_this.hmdInfo.referrer);
      console.log('dataSyn',_this.hmdInfo.inviteCode,_this.hmdInfo.referrer)
      this.magicContract.methods
        .dataSyn(_this.hmdInfo.inviteCode,_this.hmdInfo.referrer)
        .send({ from: account })
        .then((res) => {
          console.log("openLottery", res);
          // if (res.status) {
            _this.add_magicInfo();
          _this.$toast.clear();
          //  this.$toast(_this.magicInfo.inviteCode,_this.magicInfo.referrer);
          _this.$toast.success(_this.$t("message.dealSuccee2"));
          _this.add_magicInfo();
          _this.show = false;
          // }
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    withdrawUsdtFun: function () {
      if (this.magicContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.magicContract.methods
        .withdrawUsdt()
        .send({ from: account })
        .then((res) => {
          console.log("withdrawUsdt", res);
          // if (res.status) {
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
          _this.add_magicInfo();
          _this.getCoinBalance();
          // }
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    stake: function () {
      if (this.magicContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var inviteCode = _this.magicInfo.inviteCode.toString();
      var referrer = _this.magicInfo.referrer.toString();
      var account = _this.account;

      // if (!_this.isApprove) {
      //   _this.$toast(_this.$t("message.noAccredit"));
      //   return;
      // }

      if (!referrer || referrer == "--") {
        if (_this.$route.query.invite) {
          referrer = _this.$route.query.invite;
        } else if (localStorage.getItem("invite")) {
          referrer = localStorage.getItem("invite");
        } else {
          referrer = 1;
        }
      }
      if (!inviteCode || inviteCode == "--") {
        inviteCode = _this.account;
      }

      if (inviteCode == referrer) {
        referrer = "1";
      }

      if (_this.balance < 0.01) {
        _this.$toast(_this.$t("message.gas"));
        return;
      }
    
      if (!referrer || referrer == "--") {
        _this.$toast(_this.$t("message.onReferees"));
        return;
      }
      // var monye = (_this.stakeAmount * 10 ** 18).toString();
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log("investIn=======", inviteCode, referrer);
      this.magicContract.methods
        .investIn(inviteCode, referrer, "1")
        .send({
          from: account,
        })
        .then((res) => {
          console.log("investIn", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee"));
          _this.add_magicInfo();
          _this.getCoinBalance();

          _this.stakeAmount = "";
          _this.nbn = "";
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    // USDT授权
    lpApprove: function () {
      if (this.magicContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = this.magicInfo.address;
      var account = this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.usdtCoinContract.methods
        .approve(address, "5000000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("approve", res);
          this.$toast.clear();
          this.$toast.success(this.$t("message.accredit"));
          this.isApprove = true;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    // 是否授权
    isApproveFun: function () {
       if (this.magicContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var account = this.account;
      var address = this.magicInfo.address;

      this.usdtCoinContract.methods
        .allowance(account, address)
        .call()
        .then((res) => {
          console.log("usdt=========isApproveFun", account, address);
          var a = web3.utils.fromWei(res, "ether");
          if (a > 0) {
            this.isApprove = true;
          }
        });
    },
    //获取当前币种余额
    getCoinBalance: function () {
      if (this.usdtCoinContract) {
        var account = this.account;
        this.usdtCoinContract.methods
          .balanceOf(account)
          .call()
          .then((res) => {
            var num = web3.utils.fromWei(res, "ether");
            num = num.toString();
            let index = num.indexOf(".");
            if (index !== -1) {
              num = num.substring(0, 6 + index + 1);
            } else {
              num = num.substring(0);
            }
            this.coinBalance = res;
          });
      }
    },
    getUrl: function (addr) {
      var hash = window.location.hash;
      if (hash.indexOf("?") != -1) {
        hash = hash.split("?")[0];
      }
      console.log(hash.split("?")[0]);
      var url = window.location.origin + window.location.pathname + hash;
      var newAddr = "";
      if (addr) {
        newAddr = addr.replace('"', "").replace('"', "");
        this.link = url + "?invite=" + newAddr;
      }
      return this.link;
    },
    protectAddress: function (addr, num) {
      if (addr == "0x0000000000000000000000000000000000000000") {
        return "--";
      }
      if (!addr) {
        return "--";
      }
      return (addr + "").length == 42
        ? addr.substring(0, num) + "****" + addr.substring(38, 42)
        : addr;
    },
    initContent() {
      var scroll = document.getElementById("scroll");
      var content = document.getElementById("content");
      var copyContent = document.getElementById("copyContent");
      copyContent.innerHTML = content.innerHTML;
      setInterval(function () {
        //scrollTop 滚动的距离 offsetTop当前元素离顶部的距离
        if (scroll.scrollTop >= copyContent.offsetTop - content.offsetTop) {
          scroll.scrollTop = 0;
        }
        scroll.scrollTop++;
      }, 60);
    },
    spArr:function(arr, num) { //arr是你要分割的数组，num是以几个为一组
	let newArr = [] //首先创建一个新的空数组。用来存放分割好的数组
	for (let i = 0; i < arr.length;) { //注意：这里与for循环不太一样的是，没有i++
		newArr.push(arr.slice(i, i += num));
	}
	return newArr
}
  },
  mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "zhCHS");
    this.currency = "HMD";
    if (this.$route.query.invite) {
      localStorage.setItem("invite", this.$route.query.invite);
    }

  const range = (start, stop, step = 1) => Array(Math.ceil((stop - start) / step)).fill(start).map((x, y) => x + y * step);
  var listNum = range(1,5000);
  _this.arr = _this.spArr(listNum,3);
    _this.initContent();

    var getData = () => {
      this.add_magicInfo();
      this.add_usdtCoinContract();
      this.add_hmdInfo();
    };
    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        // 
        if (!this.magicContract || !this.usdtCoinContract || !_this.hmdInfo) {
          getData();
        } else {
          _this.getCoinBalance();
          _this.isApproveFun();
          if (!_this.magicInfo.referrer && _this.hmdInfo.referrer ) {
            _this.show = true;
          }
          if (_this.magicInfo.inviteCode || _this.magicInfo.referrer) {
            var invite = _this.magicInfo.inviteCode || _this.magicInfo.referrer;
            _this.getUrl(invite);
          }
          clearInterval(this.timer);
        }
      }, 1000);
    };

    getDataCalculate();
    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 80000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
  components: {
    HeadTop,
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped  lang="less">
.info {
  background: url(../../assets/img/magic/top.png) top center no-repeat,
    url(../../assets/img/flw/bottom.png) bottom center no-repeat #000000;
  background-size: 100%, 100%;
  padding: 5.5em 0 3em;
  min-height: 100%;
  box-sizing: border-box;
  .myInfo {
    background: #1c317d;
    border-radius: 10px;
    padding-bottom: 20px;
  }
  .top {
    text-align: center;
    padding: 15px 18px;
    width: 100%;
    // height: 188px;
    box-sizing: border-box;
    background: url(../../assets/img/magic/ming.png) top center no-repeat;
    background-size: 100%;
    margin: 20px auto 0;
    box-sizing: border-box;
    .topHead {
      padding: 10px 0;
    }
    .sl {
      margin-bottom: 10px;
    }
    span {
      color: #fff;
      // font-weight: bold;
      font-family: PingFangSC-Regular, PingFang SC;
      font-size: 14px;
      text-align: center;
      display: block;
      margin-top: 5px;
    }
    h1 {
      font-size: 16px;
    }
    .txt {
      margin-top: 25px;
      margin-bottom: 20px;
      h2 {
        text-align: center;
        font-size: 18px;
        margin-top: 10px;
        font-family: DINAlternate-Bold, DINAlternate;
        font-weight: bold;
        text-align: center;
        color: #ffffff;
      }
      h3 {
        font-size: 13px;
        color: #1af3f3;
        font-weight: normal;
        text-align: center;
      }
    }
    h3 {
      font-size: 13px;
      color: #1af3f3;
      font-weight: normal;
    }
  }

  .ming_bix {
    background: transparent;
    border-radius: 10px;
    overflow: hidden;
    box-sizing: border-box;
    width: 90%;
    margin: 0 auto 30px;
    margin-top: 20px;
    .list {
      background: #1e3756;
      padding: 20px 10px;
      div.van-row {
        border-bottom: 1px solid #1a2e46;
        padding: 12px 0;
      }
      span {
        color: #b57eff;
      }
      .textRight span {
        color: #fff;
      }
    }
    .tab {
      div {
        padding: 10px 0 10px;
        background: #102033;
        border-radius: 10px 10px 0 0;

        span {
          color: #5c80ac;
          font-size: 15px;
        }
      }
      .in {
        background: url(../../assets/img/flw/dd.png) top center no-repeat
          #1e3756;
        background-size: 200%;
        border-radius: 10px 10px 0 0;
        span {
          color: #fff;
          font-weight: bold;
        }
      }
    }
  }

  .title {
    text-align: center;
    margin: 30px 0 0px;
    span {
      position: relative;
      font-size: 20px;
      font-family: SourceHanSansCN-Bold, SourceHanSansCN;
      font-weight: bold;
      color: #fff;
      line-height: 39px;
      text-align: left;
      text-shadow: 4px 4px 10px #103b7e;
    }
  }

  .cent {
    // background: url(../../assets/img/flw/c.png) top center no-repeat #1e3756;
    // background-size: 100%;
    // padding: 20px;
    width: 90%;
    margin: 0 auto 30px;
    position: relative;
    box-sizing: border-box;
    border-radius: 10px;
    .minTit span::before {
      content: "";
      display: block;
      position: absolute;
      width: 8px;
      height: 10px;
      background: url(../../assets/img/magic/left.png) top center no-repeat;
      background-size: 100%;
      top: 4px;
      left: -30px;
    }
    .minTit span::after {
      content: "";
      display: block;
      position: absolute;
      width: 8px;
      height: 10px;
      background: url(../../assets/img/magic/left.png) top center no-repeat;
      background-size: 100%;
      top: 4px;
      right: -30px;
    }
    .minTit {
      text-align: center;
      margin: 30px 0 10px;
      span {
        position: relative;
        font-size: 12px;
        font-family: SourceHanSansCN-Bold, SourceHanSansCN;
        font-weight: bold;
        color: #1af3f3;
        // text-shadow: 4px 4px 10px  #103b7e;
      }
    }
    .myBox {
      position: relative;
      margin: 10px 0;
      span {
        position: absolute;
        display: block;
        text-align: center;
        width: 100%;
        bottom: 13px;
      }
    }
    .sy {
      background: url(../../assets/img/magic/sy.png) top center no-repeat;
      background-size: 100%;
      padding: 20px;
      margin-top: 20px;
      span {
        color: #1af3f3;
        font-size: 13px;
      }
      .text {
        font-size: 26px;
        font-family: DINPro;
        font-weight: bold;
        margin-top: 8px;
      }
      button {
        background: url(../../assets/img/magic/btn-1.png) top center no-repeat;
        background-size: 100%;
        border: none;
        width: 100px;
        line-height: 40px;
        height: 42px;
        text-align: center;
      }
    }

    li {
      position: relative;
      margin: 0 20px;
      padding: 15px 0;
      border-bottom: 1px solid rgb(26 46 70);
      color: #179dd9;
      span {
        font-size: 15px;
        float: right;
        color: #ffffff;
        max-width: 60%;
        text-align: right;
        overflow: hidden; /*溢出隐藏*/
        white-space: nowrap; /*规定文本不进行换行*/
        text-overflow: ellipsis;
      }
    }
    .btn {
      margin: 30px auto 20px;
      display: block;
    }
    a.btn {
      color: #fff;
    }
    // h3 {
    //   font-size: 16px;
    //   font-family: PingFangSC-Medium, PingFang SC;
    //   font-weight: 500;
    //   color: #323335;
    //   margin-bottom: 15px;
    //   font-weight: bold;
    //   margin-top: 5px;
    //   overflow: hidden; /*溢出隐藏*/
    //   white-space: nowrap; /*规定文本不进行换行*/
    //   text-overflow: ellipsis;
    //   width: 100%;
    //   margin-right: 5px;
    // }
  }
}
.btn {
  background: url(../../assets/img/flw/44414141.png) top center no-repeat;
  background-size: 100%;
  border: none;
  width: 120px;
  text-align: center;
  // font-size: 16px;
  // font-weight: bold;
  height: 42px;
  line-height: 42px;
  margin-top: 5px;
}
.tit-p {
  font-size: 13px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  line-height: 22px;
  color: #eee;
  text-align: center;
  margin: 0 20px 30px;
}
.magic {
  width: 100%;
  margin-top: 30px;
  .time {
    width: 50%;
    margin: 0 auto 10px;
    background: url(../../assets/img/magic/time.png) top center no-repeat;
    background-size: 100%;
    text-align: center;
    font-size: 13px;
    padding: 10px 0 16px;
    .van-count-down {
      display: inline;
      font-size: 16px;
      color: #fff;
    }
  }
  .magicImg {
    width: 45%;
    margin: 0 auto;
    display: block;
    position: relative;
    z-index: 2;
    animation: jump 3s ease infinite;
  }
  .magicImg2 {
    width: 45%;
    margin: 0 auto;
    display: block;
    position: relative;
    z-index: 2;
  }
  p {
    width: 60%;
    margin: 10px auto 20px;
    background: url(../../assets/img/magic/p.png) top center no-repeat;
    background-size: 100%;
    font-size: 15px;
    font-family: Source Han Sans CN;
    font-weight: bold;
    color: #11ffff;
    text-align: center;
    padding: 11px 0;
  }
  .poolNum {
    background: url(../../assets/img/magic/t.png) top center no-repeat;
    background-size: 100%;
    h3 {
      text-align: center;
      font-family: Source Han Sans CN;
      padding: 9px 0 0;
    }
    h1 {
      text-align: center;
      font-size: 28px;
      font-family: DINPro;
      color: #1af3f3;
      padding: 0.8em 0;
    }
    .blog_news_gossip {
      height: 95px;
      text-align: center;
      padding: 2em 15px 0em;
    }
    .blog_news_gossip .blog_news_tle {
      font-size: 20px;
      font-weight: bold;
      margin: 0 20px 10px;
    }
    .blog_news_gossip .blog_news_scroll {
      height: 95px;
      margin: auto;
      overflow: hidden;
    }
    .blog_news_gossip .blog_news_scroll .blog_news_box {
      padding: 0 20px;
      margin: 0;
    }
    .blog_news_gossip .blog_news_scroll .blog_news_box div {
      height: 30px;
      display: flex;
      justify-content: space-between;
      padding: 0 10px;
      margin: 10px 0;
    }
    .blog_news_gossip .blog_news_scroll .blog_news_box span {
      // width: 30%;
      // float: left;
      width: 90px;
      margin: 3px 5px 0;
      height: 25px;
      line-height: 25px;
      color: #4831be;
      display: block;
      text-align: center;
      background: url(../../assets/img/magic/1.png) top center no-repeat;
      background-size: 100%;
    }
  }
  .buy {
    background: url(../../assets/img/magic/buy.png) top center no-repeat;
    background-size: 100%;
    text-align: center;
    z-index: 2;
    position: relative;
    .box {
      padding: 1.5em 2em 0;
      font-size: 12px;
      font-family: PingFang SC;
      color: #1af3f3;
      h2 {
        font-weight: normal;
        font-size: 14px;
      }
    }
    button {
      background: url(../../assets/img/magic/btn.png) top center no-repeat;
      background-size: 100%;
      border: none;
      margin: 20px auto;
      text-align: center;
      height: 88px;
      width: 220px;
      font-size: 18px;
      font-weight: bold;
      span {
        display: block;
        font-size: 12px;
        text-align: center;
      }
    }
  }
}
.tab {
  background: url(../../assets/img/magic/tab.png) top center no-repeat;
  background-size: 100%;
  padding: 0 20px;
  height: 80px;
  h4 {
    text-align: center;
    color: #5da5ff;
    font-size: 15px;
    height: 75px;
    line-height: 60px;
  }
  .in {
    background: url(../../assets/img/magic/tab-in.png) top center no-repeat;
    background-size: auto 100%;
    h4 {
      color: #1af3f3;
      font-size: 18px;
    }
  }
}
.inner-container {
  -webkit-animation: 10s myMove linear infinite normal;
  animation: 10s myMove linear infinite normal;
}
/*文字无缝滚动*/
@keyframes myMove {
  0% {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
  100% {
    -webkit-transform: translate3d(0, -1710px, 0);
    transform: translate3d(0, -171px, 0);
  }
}
.muen {
  position: absolute;
  right: 0;
  z-index: 3;
  .tab1 {
    background: #3745b0;
    word-wrap: break-word;
    width: 12px;
    padding: 15px;
    border-radius: 20px 0px 0px 20px;
    margin: 5px 0;
    font-weight: bold;
    //     position: absolute;
    // right: 0;
  }
  .tab1.in {
    background: linear-gradient(to right, #11ffff, #0869ff);
    // padding: 15px 15px 15px 20px;
  }
}

@keyframes jump {
  0% {
    transform: translateY(0) scale(1, 1);
  }
  /* 中间状态图片位移并且拉伸 */
  50% {
    transform: translateY(-30px) scale(0.95, 1.03);
  }
  100% {
    transform: translateY(0) scale(1, 1);
  }
}
.jmpy {
  position: relative;
  margin-top: 50px;
}
.kai {
  position: relative;
  margin: 30px 0;
  .pai {
    width: 190px;
    margin: -20px auto 0;
    height: 60px;
    background: url(../../assets/img/magic/pai.png) top center no-repeat;
    background-size: 100%;
    font-size: 42px;
    font-family: DINPro;
    font-weight: bold;
    color: #873714;
    text-align: center;
    line-height: 60px;
    z-index: 3;
    position: relative;
  }
}
.jmpy::after {
  content: "";
  position: absolute;
  display: block;
  width: 100px;
  height: 50px;
  left: 50%;
  margin-left: -50px;
  bottom: -10px;
  // z-index: -1;
  background: rgb(55 69 176 / 75%);
  filter: blur(10px);
  border-radius: 50%;
  /* 设置投影动画 */
  animation: shadow 3s ease infinite;
}
@keyframes shadow {
  0% {
    transform: scale(1);
    opacity: 1;
    filter: blur(10px);
  }
  /* 投影缩放+虚化 */
  50% {
    transform: scale(0.8);
    opacity: 0.7;
    filter: blur(20px);
  }
  100% {
    transform: scale(1);
    opacity: 1;
    filter: blur(10px);
  }
}
.tips {
  width: 330px;
  height: 224px;
  top: 200px;
  padding: 100px 0 0;
  box-sizing: border-box;
  margin: 0 auto;
  position: relative;
  background: url(../../assets/img/magic/tips.png) top center no-repeat;
  background-size: 100%;
  text-align: center;
  p {
    text-align: left;
    color: #1af3f3;
    font-size: 14px;
    margin-bottom: 5px;
    padding: 0 20px;
    line-height: 24px;
  }
}

@media (min-width: 390px) and(max-width:400px) {
  .magic .poolNum .blog_news_gossip,
  .magic .poolNum .blog_news_gossip .blog_news_scroll {
    height: 85px;
  }
  .tab h4 {
    height: 70px;
  }
}
@media (min-width: 370px) and(max-width:380px) {
  .magic .poolNum h1 {
    padding: 0.6em 0;
  }
  .magic .poolNum .blog_news_gossip {
    padding: 2em 15px 0em;
  }
  .magic .poolNum .blog_news_gossip,
  .magic .poolNum .blog_news_gossip .blog_news_scroll {
    height: 88px;
  }
  .buy {
    z-index: 2;
    position: relative;
  }
  .tab h4 {
    height: 70px;
  }
}

@media (min-width: 320px) and(max-width:361px) {
  .magic .poolNum h1 {
    padding: 0.7em 0;
  }
  .magic .poolNum .blog_news_gossip {
    height: 84px;
    text-align: center;
    padding: 1.5em 0 0;
  }
  .magic .buy .box {
    padding: 1.2em 2em 0;
  }
  .magic .buy button {
    width: 200px;
    height: 80px;
  }
  .tab h4 {
    height: 66px;
  }
}
</style>



