import Vue from 'vue'


const state = {
  /*存储用户信息全局变量*/
  account: JSON.parse(getStorage('account') || 0),
  balance: JSON.parse(getStorage('balance') || 0),
  hmdBalance: JSON.parse(getStorage('hmdBalance') || 0),
  aesBalance: JSON.parse(getStorage('aesBalance') || 0),
  GBalance: JSON.parse(getStorage('GBalance') || 0),
  mxtCoinContract: JSON.parse(getStorage('mxtCoinContract') || null),//质押的usdt
  mxtContract: JSON.parse(getStorage('mxtContract') || null),
  hmdInfo: JSON.parse(getStorage('hmdInfo') || null),
  FiveCoinContract: JSON.parse(getStorage('FiveCoinContract') || null),//five币合约
  GCoinContract: JSON.parse(getStorage('GCoinContract') || null),//five币合约
  price:JSON.parse(getStorage('price') || null),
  magicContract:JSON.parse(getStorage('magicContract') || null),
  magicInfo:JSON.parse(getStorage('magicInfo') || null),
  NFTContract:JSON.parse(getStorage('NFTContract') || null),
  NFTInfo:JSON.parse(getStorage('NFTInfo') || null),
  usdtCoinContract:JSON.parse(getStorage('usdtCoinContract') || null),
}

const getters = {
  /*获取用户信息*/
  getIsApprove: function (state) {
    let isApprove = null;
    if (state.isApprove) {
      isApprove = state.isApprove;
    } else {
      isApprove = JSON.parse(getStorage('isApprove') || null);
    }
    return isApprove
  },

}

const mutations = {

  set_account: (state, data) => {
    state.account = data;
  },
 
  set_balance: (state, data) => {
    state.balance = data;
  },
  set_hmdBalance: (state, data) => {
    state.hmdBalance = data;
  },
  // usdt测试币
  set_mxtCoinContract: (state, data) => {
    state.mxtCoinContract = data;
  },
 
  // 矿池
  set_hmdInfo: (state, data) => {
    state.hmdInfo = data;
  },
  set_mxtContract: (state, data) => {
    state.mxtContract = data;
  },
  // 单价列表
  set_price: (state, data) => {
    state.price = data;
  },
  set_aesBalance:(state, data) => {
    state.aesBalance = data;
  },
  set_FiveCoinContract:(state, data) => {
    state.FiveCoinContract = data;
  },
  // G币
  set_GCoinContract:(state, data) => {
    state.GCoinContract = data;
  },
    // G币余额
    set_GBalance:(state, data) => {
    state.GBalance = data;
  },

      // G币余额
      set_magicContract:(state, data) => {
        state.magicContract = data;
      },
      set_magicInfo:(state, data) => {
        state.magicInfo = data;
      },
      set_usdtCoinContract:(state, data) => {
        state.usdtCoinContract = data;
      },
       //nft
       set_NFTContract:(state, data) => {
        state.NFTContract = data;
      },
      set_NFTInfo:(state, data) => {
        state.NFTInfo = data;
      },
}

