    getWeb3.Init(priceABI, '0x5d1F23b1564ce7A00C94f9Fa970D9c630369CA72', (contract, addr, balance, id) => {
      commit('set_netWork', id)
      //USDT
      contract.methods.getCurrentPrice('0x382bb369d343125bfb2117af9c149795c6c65c50').call().then((res) => {
        commit('set_priceUSDT', res / 10 ** 18)
      })
      // KSPWA
      contract.methods.getCurrentPrice('0xab0d1578216a545532882e420a8c61ea07b00b12').call().then((res) => {
        commit('set_priceKSWAP', res / 10 ** 18)
      })
    })
    fetch('https://www.cherryswap.net/api/price?index=0').then(response => response.json())//解析为可读数据
