<template>
  <div class="info">
    <HeadTop :lang="lang" @changeAppLocale="changeAppLocale" :titleHead="this.$t('message.f_61_2')"/>

    <div class="cent2">
        <ul>
          <li>{{ $t("message.f_39") }}<span>{{ this.hmdInfo ? protectAddress(hmdInfo.referrer, 4) : "--"}}</span></li>
          <li>{{ $t("message.f_40") }}<span>{{ this.link }}</span></li>
        </ul>
       <!-- <div class="refe">推荐节点<span>{{ this.hmdInfo ? protectAddress(hmdInfo.referrer, 4) : "--"}}</span></div> -->
        <h2 class="title"><span>{{ $t("message.f_41") }}</span>  </h2>
        <div class="codeImg" style="width:200px;height:200px;margin:.15rem auto;padding
        30px;" id="qrCode"></div>

        <!-- <div class="my">{{ this.link }}</div>
        <span>邀请链接</span> -->
    </div>
   

    <div class="erm">
      <img src="../../assets/img/flw/f.png" alt="" width="100">
         <van-row type="flex" justify="space-between" v-if="this.hmdInfo">
          <van-col span="24" class="textCenter">
            <button
              v-clipboard:copy="link"
              v-clipboard:success="onCopy"
              v-clipboard:error="onError"
              class="btn"
            >
              {{ $t("message.f_42") }}
            </button></van-col
          >
        </van-row>
        <!-- <div class="my">{{ this.link }}</div> -->
    </div>



    
  </div>
