<template>
  <header>
    <div id="header">
      <div class="banle" v-if="isHome">
        <van-row>
          <van-col span="4">
            <a v-if="isHome">
              <img
                @click="hreftwo('/')"
                style="width: 100px; margin-left: 5px"
                src="../assets/img/flw/logo.png"
              />
            </a>
            <a v-else>
              <img
                @click="hreftwo('/')"
                style="width: 12px; margin-top: 5px"
                src="../assets/img/flw/j.png"
              />
            </a>
          </van-col>
          <van-col span="16" class="textCenter">
            <span v-if="!isHome" style="font-size: 16px">挖矿数据</span>
          </van-col>
          <van-col span="4" @click="showPopup" class="textRight">
            <img src="../assets/img/flw/mune.png" alt="" width="30" />
          </van-col>
        </van-row>
      </div>

      <div
        v-else
        class="banle"
        :class="titleHead?'in' : 'out'"
      >
        <van-row>
          <van-col span="4">
            <a v-if="isHome">
              <img
                @click="hreftwo('/')"
                style="width: 100px; margin-left: 5px"
                src="../assets/img/flw/logo.png"
              />
            </a>
            <a v-else>
              <img
                @click="hreftwo('/')"
                style="width: 12px; margin-top: 5px"
                src="../assets/img/flw/j.png"
              />
            </a>
          </van-col>
          <van-col span="16" class="textCenter">
            <span v-if="!isHome && titleHead" style="font-size: 16px">{{
              titleHead
            }}</span>
          </van-col>
          <van-col span="4" @click="showPopup" class="textRight">
            <img src="../assets/img/flw/mune.png" alt="" width="30" />
          </van-col>
        </van-row>
      </div>

      <div class="arrd" v-if="isHome">
        <div class="bgWhite flex">
          <div class="bg_logo">
            <img src="../assets/img/topLogo.png" alt="" />
            <span>BSC</span>
          </div>
          <p>{{ protectAddress(this.account, 10) }}</p>
        </div>
      </div>

      <van-popup
        v-model="show"
        position="right"
        :style="{ height: '100%', width: '50%', background: '#15273D' }"
      >
        <div class="menu">
          <img src="../assets/img/flw/logo.png" alt="" width="55%" />
          <div class="list">
            <div class="li_col">
              <img src="../assets/img/flw/icon-1.png" alt="" width="20" />
              <a @click="hreftwo('/')">{{ $t("message.f_58") }}</a>
            </div>
            <div class="li_col">
              <img src="../assets/img/flw/icon-2.png" alt="" width="20" />
              <a @click="hreftwo('/pool')">{{ $t("message.f_59") }}</a>
            </div>
            <div class="li_col">
              <img src="../assets/img/flw/icon-3.png" alt="" width="20" />
              <a @click="hreftwo('/info')">{{ $t("message.f_60") }}</a>
            </div>
            <div class="li_col">
              <img src="../assets/img/flw/icon-5.png" alt="" width="20" />
              <a @click="hreftwo('/ming')">{{ $t("message.f_61") }}</a>
            </div>
            <div class="li_col">
              <img src="../assets/img/flw/icon-6.png" alt="" width="20" />
              <a @click="hreftwo('/invitation')">{{ $t("message.f_61_2") }}</a>
            </div>
            <div class="li_col">
              <img src="../assets/img/flw/icon-4.png" alt="" width="20" />
              <a>{{ $t("message.f_61_y") }}</a>
              <ul>
                <li
                  :class="lang == 'zhCHS' ? 'in' : ''"
                  @click="changeLocale('zhCHS')"
                >
                  {{ $t("message.f_61_3")
                  }}<img
                    v-if="lang == 'zhCHS'"
                    class="gou"
                    src="../assets/img/gou.png"
                  />
                </li>
                <li
                  :class="lang == 'en' ? 'in' : ''"
                  @click="changeLocale('en')"
                >
                  {{ $t("message.f_61_4") }}
                  <img
                    v-if="lang == 'en'"
                    class="gou"
                    src="../assets/img/gou.png"
                  />
                </li>
              </ul>
            </div>
            <div class="li_col">
              <img src="../assets/img/flw/icon-7.png" alt="" width="20" />
              <a @click="hreftwo('/magic')">{{ $t("message.f_61_5") }}</a>
            </div>
            <div class="li_col">
              <img src="../assets/img/flw/icon-8.png" alt="" width="20" />
              <a @click="tips">{{ $t("message.f_62") }}</a>
            </div>
            <div class="li_col">
              <img src="../assets/img/flw/icon-9.png" alt="" width="20" />
              <a @click="tips">{{ $t("message.f_63") }}</a>
            </div>
            <div class="li_col">
              <img src="../assets/img/flw/icon-12.png" alt="" width="20" />
              <a href="https://www.flowcoin.live">{{ $t("message.f_64") }}</a>
            </div>
          </div>
        </div>
      </van-popup>
    </div>
  </header>
</template>

<script>
import { mapActions, mapState } from "vuex";
export default {
  data() {
    return {
      isShowNav: false,
      showRule: false,
      show: false,
      lang_1: null,
      langList: [
        {
          text: "中文",
          img_txt: "zhCHS",
        },
        {
          text: "EN",
          img_txt: "en",
        },
      ],
    };
  },
  props: {
    lang: {
      type: String,
      default: "zhCHS",
    },
    type: {
      type: String,
      default: "home",
    },
    titleHead: {
      type: String,
      default: "",
    },
    isHome: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    ...mapState("main", ["account", "oktInfo"]),
  },
  methods: {
    ...mapActions("main", ["add_oktInfo"]),
    showPopup() {
      this.show = true;
    },
    protectAddress: function (addr, num) {
      if (addr == "0x0000000000000000000000000000000000000000") {
        return "--";
      }
      if (!addr) {
        return "--";
      }
      return (addr + "").length == 42
        ? addr.substring(0, num) + "****" + addr.substring(38, 42)
        : addr;
    },
    changeLocale(locale) {
      localStorage.setItem("lang", locale);
      this.$emit("changeAppLocale", locale);
      // this.isShowNav = !this.isShowNav;
    },
    LanguageClick: function () {
      this.isShowNav = !this.isShowNav;
    },
    tips() {
      this.$toast.fail(this.$t("message.tips3"));
      // this.$dialog.alert({
      //   message: this.$t("message.tips3"),
      //   theme: "round-button",
      // });
    },
    prev() {
      this.$router.go(-1);
    },

    // 	showPopup() {
    //   this.showRule = true;
    // },
    hreftwo(url, title) {
      // console.log(url.length);
      // if (url.length > 1) {
      //   this.$router.push({ path: url + "/" + this.lang });
      // } else {
      if (url.length > 2) {
        this.$router.push({ path: url });
      } else {
        this.$router.push({ path: url });
      }

      // }
    },
  },
};
</script>

<style scoped lang="less">
.banle {
  padding: 20px 3% 10px;
  top: 0;
  width: 100%;
  box-sizing: border-box;

}
.banle.in{
position: fixed; background: #15273d; z-index: 12
}
.banle.out{
  position: fixed; z-index: 12;
  background: url(../assets/img/magic/head.png) top center no-repeat;
  background-size: 100%;
  padding-bottom: 45px;
}
.bgWhite {
  width: 60%;
  // height: 60px;
  margin: 15px auto 0;
  padding: 4px;
  background: #1d3553;
  opacity: 0.8;
  border-radius: 30px;
  p {
    margin-left: 10px;
    height: 25px;
    line-height: 25px;
  }
}
.flex {
  display: flex;
}
.bg_logo {
  height: 25px;
  line-height: 25px;
  font-size: 14px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 600;
  color: #fff;
  background: #27456a;
  border-radius: 4px;
  border-radius: 30px;
  padding: 4px;
  padding: 0 4px;
  img {
    width: 20px;
    vertical-align: sub;
  }
}
.menu {
  text-align: center;
  padding: 40px 0px;
  position: relative;
  height: 90%;
  .list {
    border-top: 1px solid #000001;
    margin-top: 40px;
    padding: 20px;
    img {
      vertical-align: bottom;
      float: left;
      margin-top: 15px;
    }
    a {
      font-size: 16px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 500;
      color: #62e6f3;
      display: block;
      padding: 12px 30px 12px 30px;
    }
  }
  .li_col li.in {
    color: #4da9db;
  }
  .li_col li {
    padding: 5px 26px;
    color: #999;
    .gou {
      width: 20px;
      float: none;
      margin-top: 0;
      margin-left: 20px;
    }
  }
  .foot {
    position: absolute;
    bottom: 20px;
    left: 20px;
    img {
      display: inline-block;
      margin-right: 8px;
    }
  }
}
</style>



