<template>
  <div class="info">
    <HeadTop :lang="lang" @changeAppLocale="changeAppLocale" :titleHead="this.$t('message.f_59')"/>
    <div class="top">
      <!-- <div class="sl">
        <span>{{ $t("message.f_12") }}</span>
        <h2>{{ hmdInfo ? hmdInfo.info[0] : "0" | figure }}</h2>
      </div> -->
      <van-row gutter="20" type="flex" justify="space-between">
        <van-col span="12">
          <h4>{{ $t("message.f_43") }}</h4>
        </van-col>
        <van-col span="12" class="textRight">
          <h1 class="textRight" style="font-size:14px">310,000,000 FLW</h1>
        </van-col>
      </van-row>
      <van-row gutter="20" type="flex" justify="space-between">
        <van-col span="12"  class="textCenter">
          <!-- <span>{{ $t("message.f_29_1") }}</span> -->
          <h3>{{ $t("message.f_44") }}(FLW)</h3>
          <h2>{{ hmdInfo ? (Number(hmdInfo.info[3]) - Number(hmdInfo.daichan)) : "0" | figureK }}</h2>
        </van-col>
        <van-col span="12" class="textCenter">
          <h3>{{ $t("message.f_45") }}(FLW)</h3>
          <h2>
            {{ hmdInfo ? hmdInfo.info[16] : "0" | figureK }}
          </h2>
        </van-col>
      </van-row>
    </div>
  <p style="width: 360px;color:#eee;margin:0 auto;color:rgb(97,131,173);text-align: center;">{{ $t("message.f_45_r") }}</p>
      <h1 class="textCenter title">
        <span>{{ $t("message.f_46") }}</span>
      </h1>
    <div class="cent">
     
      <div class="">
        <ul>
          <li>{{ $t("message.f_46_1") }}<span>{{
            this.hmdInfo ? protectAddress('0x3d5821dbcb4c7ecc7b06b8016468e98180e8b638', 8) : "--"
          }}</span></li>
          <li>{{ $t("message.f_47") }}<span><a href="https://bscscan.com/address/0x3d5821dbcb4c7ecc7b06b8016468e98180e8b638#code">{{ $t("message.f_48") }}>></a></span></li>
          <li>{{ $t("message.f_49") }}<span><a href="https://bscscan.com/token/0x3d5821dbcb4c7ecc7b06b8016468e98180e8b638?a=0x000000000000000000000000000000000000dead">{{
            this.hmdInfo ? protectAddress('0x000000000000000000000000000000000000dEaD', 8) : "--"
          }}</a></span></li>
          <li>{{ $t("message.f_50") }}<span><a href="https://bscscan.com/token/0x3d5821dbcb4c7ecc7b06b8016468e98180e8b638#balances">{{ $t("message.f_51") }}>></a></span></li>
          <li>{{ $t("message.f_a_1") }}<span>{{
            this.hmdInfo ? protectAddress(hmdInfo.address, 8) : "--"
          }}</span></li>
          <li>{{ $t("message.f_a_2") }}<span>147,949,632.00 FLW</span></li>
          <li>{{ $t("message.f_a_3") }}<span>{{ hmdInfo ? hmdInfo.info[4] : "0" | figureK }} FLW</span></li>
          <li>{{ $t("message.f_a_4") }}<span>{{ hmdInfo ? hmdInfo.info[16] : "0" | figureK }} FLW</span></li>
          <li>{{ $t("message.f_a_5") }}<span>{{ hmdInfo ? hmdInfo.info[20] : "0" | setTime}}</span></li>
          <li>{{ $t("message.f_a_6") }}<span>{{ hmdInfo ? hmdInfo.info[27]  : "0" | figureK}} FLW</span></li>
          <li>{{ $t("message.f_4") }}<span>{{ hmdInfo ? hmdInfo.info[0] : "0" | figureK }} FLW</span></li>
        </ul>
         <van-row type="flex" justify="space-between" v-if="this.hmdInfo">
          <van-col span="24"
            ><a
              :href="'https://bscscan.com/address/' + this.hmdInfo.address"
              class="btn"
              >{{ $t("message.f_a_7") }}</a
            ></van-col
          >
        </van-row>
      </div>
    </div>
  </div>
</template>

<script type="es6">
import HeadTop from "../../components/Header";
import { mapActions, mapState } from "vuex";

export default {
  data() {
    return {
      lang: "",
      //   currency: "",
      link: "--",
      //   coinBalance: 0,
      //   isApprove: false,
      //   stakeAmount: 1,
      //   withdrawAmount: 0,
      timer: null,
      timer2: null,
      TokenPrice: 0,
      tabsNum: 1,
      F5data: {
        allBonus: 0,
        balanceBonus: 0,
        vipLength: 0,
      },
    };
  },
  computed: {
    ...mapState("main", ["hmdInfo", "mxtContract", "account", "aesBalance"]),
  },
  methods: {
    ...mapActions("main", [
      "add_hmdInfo",
      "add_mxtCoinContract",
      "add_aesCoinContract",
    ]),
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
    getUsdt(num) {
      return num * this.hmdInfo.fivePrice;
    },
    // 复制成功时的回调函数
    onCopy(e) {
      var txt = this.$t("message.copySuccee");
      this.$toast.success(txt);
    },
    // 复制失败时的回调函数
    onError(e) {
      var txt = this.$t("message.copyFail");
      this.$toast.fail(txt);
    },
    getUrl: function (addr) {
      var hash = window.location.hash;
      if (hash.indexOf("?") != -1) {
        hash = hash.split("?")[0];
      }
      var url = window.location.origin + window.location.pathname + "#/";
      var newAddr = "";
      if (addr) {
        newAddr = addr.replace('"', "").replace('"', "");
        this.link = url + "?invite=" + newAddr;
      }
      return this.link;
    },
    protectAddress: function (addr, num) {
      if (addr == "0x0000000000000000000000000000000000000000") {
        return "--";
      }
      if (!addr) {
        return "--";
      }
      return (addr + "").length == 42
        ? addr.substring(0, num) + "****" + addr.substring(38, 42)
        : addr;
    },
  },
  mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "zhCHS");
    this.currency = "HMD";
    if (this.$route.query.r) {
      localStorage.setItem("r", this.$route.query.r);
    }
    var getData = () => {
      this.add_hmdInfo();
      this.add_mxtCoinContract();
      this.add_aesCoinContract();
    };
    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.hmdInfo || !this.mxtContract) {
          getData();
        } else {
          _this.dataInfo = _this.hmdInfo.info;
          // _this.getPrice();
          if (_this.hmdInfo.inviteCode || _this.hmdInfo.referrer) {
            var invite = _this.hmdInfo.inviteCode || _this.hmdInfo.referrer;
            _this.getUrl(invite);
          }
          clearInterval(this.timer);
        }
      }, 1000);
    };

    getDataCalculate();
    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 10000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
  components: {
    HeadTop,
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped  lang="less">
.info {
  background:url(../../assets/img/flw/bottom.png) bottom center no-repeat #000000;
   background-size: 100%;
  padding: 5.5em 0 3em;
  min-height: 100%;
    box-sizing: border-box;
  .top {
    text-align: center;
    padding: 21px;
    width: 350px;
    // height: 188px;
    box-sizing: border-box;
    background: url(../../assets/img/flw/top_bg.png) top center no-repeat;
    background-size: 100%;
    margin: 5px auto 10px;
    border-radius: 10px;
    box-sizing: border-box;
    .sl {
      margin-bottom: 10px;
    }
    span {
      color: #fff;
      // font-weight: bold;
      font-family: PingFangSC-Regular, PingFang SC;
      font-size: 14px;
    }
    h3{
      font-size: 13px;
      text-align: center;
      font-weight: normal;
      margin-top: 35px;
      img{
        vertical-align: middle;
      }
    }
    h2 {
      font-size: 18px;
      margin-top: 10px;
      margin-bottom: 12px;
      font-family: DINAlternate-Bold, DINAlternate;
      font-weight: bold;
      text-align: center;
      color: #ffffff;
    }
  }


  .ming_bix {
    background:transparent;
    border-radius: 10px;
    overflow: hidden;
    box-sizing: border-box;
    width: 90%;
    margin: 0 auto 30px;
    margin-top: 20px;
    .list{
        background:#1E3756;
        padding: 20px 10px;
        div.van-row{
              border-bottom: 1px solid #1a2e46;
    padding: 12px 0;
        }
        span{
          color: #B57EFF;
        }
        .textRight span{
          color: #fff;
        }
        
    }
    .tab {
      div {
        padding: 10px 0 10px;
        background:#102033;
        border-radius: 10px 10px 0 0;
      
        span {
          color: #5C80AC;
          font-size: 15px;
        }
      }
      .in {
        background: url(../../assets/img/flw/dd.png) top center no-repeat #1e3756;
        background-size: 200%;
        border-radius: 10px 10px 0 0;
        span {
          color: #fff;
          font-weight: bold;
        }
      }
    }
  }
.title span::before {
  content: "";
  display: block;
  position: absolute;
  width: 44px;
  height: 10px;
  background: url(../../assets/img/flw/left3.png) top center no-repeat ;
  background-size: 100%;
  top: 10px;
  left: -60px;
}
   .title span::after {
      content: "";
      display: block;
      position: absolute;
      width: 36px;
      height: 10px;
      background: url(../../assets/img/flw/right3.png) top center no-repeat;
      background-size: 100%;
      top: 10px;
      right: -50px;
    }
    .title {
      text-align: center;
      margin: 30px 0 10px;
      span {
        position: relative;
        font-size:20px;
        font-family: SourceHanSansCN-Bold, SourceHanSansCN;
        font-weight: bold;
        color: #fff;
        line-height: 39px;
        text-align: left;
        text-shadow: 4px 4px 10px  #103b7e;
      }
    }

  .cent {
    background: url(../../assets/img/flw/c.png) top center no-repeat #1e3756;
    background-size: 100%;
    padding: 20px;
    width: 90%;
    margin: 0 auto 30px;
    position: relative;
    box-sizing: border-box;
    border-radius: 10px;
   
    span {
      font-size: 12px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: #aba8a8;
    }
      li::after {
      content: "";
      display: block;
      position: absolute;
      width: 12px;
      height: 12px;
      background: url(../../assets/img/flw/dian.png) top center no-repeat;
      background-size: 100%;
      top: 15px;
      left: -25px;
    }
    li{
      position: relative;
      margin-left: 20px;
      padding: 15px 0;
      border-bottom:1px solid rgb(26 46 70);
      color: #62E6F3;
      span{font-size: 15px;
        float: right;
        color: #FFFFFF;
        max-width: 60%;
        text-align: right;
       overflow: hidden; /*溢出隐藏*/
      white-space: nowrap; /*规定文本不进行换行*/
      text-overflow: ellipsis;
     a{
       color: #f9c7af;
     }
        
      }
    }
    .btn{
      margin: 30px auto 20px;
      display: block;
 
    }
       a.btn{color: #fff;}
    // h3 {
    //   font-size: 16px;
    //   font-family: PingFangSC-Medium, PingFang SC;
    //   font-weight: 500;
    //   color: #323335;
    //   margin-bottom: 15px;
    //   font-weight: bold;
    //   margin-top: 5px;
    //   overflow: hidden; /*溢出隐藏*/
    //   white-space: nowrap; /*规定文本不进行换行*/
    //   text-overflow: ellipsis;
    //   width: 100%;
    //   margin-right: 5px;
    // }
  }
}
.btn {
    display: inline-block;
    width: 100%;
    margin: 0 auto;
    display: block;
    background: url(../../assets/img/flw/btn.png) top center no-repeat;
    background-size: 100%;
    border: none;
    text-align: center;
    color: #fff;
    font-family: PingFangSC-Medium, PingFang SC;
    font-weight: 500;
    font-size: 17px;
    padding: 1em 0 1.5em;
}
</style>



