import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/page/home/home'
import Info from '@/page/home/info'
import Ming from '@/page/home/ming'
import Nft from '@/page/home/nft'
import Pool from '@/page/home/pool'
import Magic from '@/page/home/magic'

import Invitation from '@/page/home/invitation'
Vue.use(Router);


//获取原型对象上的push函数
const originalPush = Router.prototype.push
    //修改原型对象中的push方法
Router.prototype.push = function push(location) {  
    return originalPush.call(this, location).catch(err => err)
}

export default new Router({
    routes: [
        {
            path: '/',
            name: 'index',
            component: Home
        },
        {
            path: '/pool',
            name: 'pool',
            component: Pool
        },
        {
            path: '/invitation',
            name: 'invitation',
            component: Invitation
        },
        {
            path: '/info',
            name: 'info',
            component: Info
        },
        {
            path: '/ming',
            name: 'ming',
            component: Ming
        },
        {
            path: '/magic',
            name: 'magic',
            component: Magic
        },
        {
            path: '/nft',
            name: 'nft',
            component: Nft
        },
    ]
})


